body {
  margin: 0;
  padding: 0;
  background-color: #e9ebee;
  font-family: Helvetica, Arial, sans-serif;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
header{
  background: #3b5998;
  position: fixed;
  z-index: 99999;
  width: 100%;
  top: 0;
}

main{
  padding: 50px 15px 0 18px;
  display: flex;
  height: 100vh;
}

.scroll-y{
  overflow-y: scroll;
}

/*Remove the scrollbar from Chrome, Safari, Edge and IE*/
::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

* {
-ms-overflow-style: none !important;
}
.container{
  width: 100%;
  padding: 20px;
}
.inline{
  display: inline-flex;
}

.all-grow> a,
.all-grow> div{    
  flex-grow: 1;
}
img.responsive{
  max-width: 100%;
  height: auto;
}
.left{
  float: left;
}
.right{
  float: right;
}
.clearfix{
  clear: both;
}

.height-full{
  height: 100%;
}

.width-full{
  width: 100%;
}

.text-center{
  text-align: center;
}

.circle{
  border-radius: 100%;
}
.no-margin{
  margin: 0;
}

.align-center{
  align-items:center;
}

.push-right{
  justify-content: right;
}
.space-between{
  justify-content: space-between;
}

.fa-smile{
  color: #ffd972;
}

.fa-thumbs-up{
  color: #365899;
}

/* Small screens */
@media screen and (max-width: 990px) {
  .large-only{
    display: none;
  }
}

/* Large screens */
@media screen and (min-width: 990px) {
  .small-only{
    display: none;
  }
  .large-only{
    display: block;
  }
  .large-only.inline{
    display: flex;
  }
  .container{
    width: 90%;
    max-width: 1200px;
  }
}


.Navbar {
    height: 44px;
    margin: 0 auto;
    padding: 0 15px;
    position: relative;
    z-index: 12;
    box-sizing: border-box;
}

.Navbar .menu a{
    color: #162643;
    font-size: 1.5rem;
    text-decoration: none;
    padding: 8px;
    cursor: pointer;
}

.Navbar .menu a.active{
    color: white;
}
.Navbar .menu a:hover{
    background: rgba(0, 0, 0, .1); 
}
.Navbar .large-only.menu.width-full>div{
    height: 100%;
    align-items: center;
    width: 48%;
}

.Navbar .searchbar{
    position: relative;
    background-color: white;
    padding: 2px 5px;
    width: 100%;
    display: inline-flex;
}

.Navbar .searchbar input{
    font-size: 1rem;
    outline: none; 
    padding: 1px 5px; 
    font-size: 16px;
    border: 0;
    flex-grow: 1;
}


.Navbar .menu .small-text{
    justify-content: flex-end;
}

.Navbar .menu .small-text a{
    font-size: 0.8rem;
    font-weight: bold;
    color: white;
}
.Navbar .menu .small-text .icons a{
    font-size: 1.2rem;
    color: #1a2947;
}
.Navbar .logo{
    padding-top: 2px;
}
.Navbar .logo a{
    color: white;
    font-size: 1.6rem;
}
.img-text a{
    display: inline-flex;
    align-items: center;
    text-decoration: none;
}
.img-text img{
    width: 25px;
    height: 25px;
    margin: 0 5px;
}
.storyboard .conf{
    padding: 10px 0;
}
.storyboard .conf a{
    text-decoration: none;
    color: #365899;
    font-size: 0.8rem;
} 
.storyboard .stories{
    margin: 10px 0;
    padding: 10px 0;
}
.storyboard .stories .story{
    padding: 10px 0;
}
.storyboard .stories .story .icon{
    width: 50px;
    height: 50px;
}
.storyboard .stories .story .text{
    padding-left: 10px;
}
.storyboard .stories .story .text .time{
    font-size: 0.6rem;
    font-weight: bold;
}
.sidebar{
    width: 20%;
    text-decoration: none;
    font-size: 16px;
}

.sidebar .bold{
    font-weight: bold;
    font-size: 14px;
}

.sidebar .grpTitle{
    margin-top: 20px;
}

.sidebar .grpTitle .img-text a{
    color: #1a2947;
    font-size: 13px;
    padding: 7px 0;
}
.sidebar .grpTitle .img-text img{
    width: 20px;
    height: 20px;
}




.sidebar.storyboard{
    background-color: white;
    padding: 0 10px;

}








/* Small screens */
@media screen and (max-width: 990px) {
    .sidebar{
      display: none;
    }
  }
.posts-container{
    flex-grow: 3;
    padding: 0 10px;
    display: block;
}

.posts-container .post{
    position: relative;
    background-color: white;
}

.posts-container .post .title{
    padding: 10px;
} 

.posts-container .post .title img{
    width: 40px;
    height: 40px;
} 

.posts-container .post .title a span:last-child{
    position: absolute;
    color: #365899;
    font-weight: bold;
    font-size: 0.85rem;
    top: 15px;
    left: 60px;
}

.posts-container .post .title>.postTime{
    top: 35px;
    left: 60px;
    font-weight: bold;
    font-size: .6em;
    position: absolute;
    
}

.posts-container .post .title .more{
    top: 20px;
    right: 10px;
    font-weight: bold;
    color: gray;
    cursor: pointer;
    font-size: 1em;
    position: absolute;
    
}

.posts-container .post .title .more:hover{
    color: black;
}

.posts-container .post .actions .emo{
    padding: 10px 10px 0px 10px;
}
.posts-container .post .actions .text{
    margin-left: 10px;
    font-size: 0.8rem;
}
.posts-container .post .actions hr{
    margin: 15px 0;
}
